
.imageBackground{
    width:100%;
    height: 100vh;
    object-position: center;
    object-fit: cover;
    opacity: 0.35;
    filter: grayscale(75%) blur(1.25px);


}

form {
    background-color: white;
    padding: 1rem;
    border-radius: 15px;
    border: none;
    box-shadow: 5px 5px 5px rgb(0,0,0,0.35);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.shortForm {
    margin: 10px 0px 5px 0px;
    text-indent: 10px;
    font-size: 14px;
    width: 250px;
    height: 30px;
    border-radius: 5px;
    border: none;
    background-color: rgb(211, 211, 211, 0.75);
    color: black;
}

.shortForm:focus {
    outline-color: #008fb3
    ;
}

input:placeholder-shown,
textarea:placeholder-shown {
    color: darkgray;
}

.button {
    margin: .5rem auto;
    display: inline-block;
    padding: .5rem 1rem;
    border-radius: 10rem;
    color: #fff;
    text-transform: uppercase;
    font-size: .75rem;
    letter-spacing: .15rem;
    transition: all .3s;
    position: relative;
    overflow: hidden;
    z-index: 1;
    border: none;
}

.button:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #0cf;
    border-radius: 10rem;
    z-index: -2;
}

.button:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: #008fb3;
    transition: all .3s;
    border-radius: 10rem;
    z-index: -1;
}

.button:hover {
    color: #fff;
    cursor: pointer;
}

.button:hover:before {
    width: 100%;
}

* {
    font-family: Arial;
    text-decoration: none;
    font-size: 20px;
}

.button:focus {
    border: none !important;
    outline: none !important;
}

.button:active {
    transform: scale(.75)
}

.buttonContainer {
    display: flex;
    justify-content: center;
}

.isLoading{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    animation: hearts 0s 1s forwards;
}

.isLoading IMG{
    width:300px;
}

@keyframes hearts{
    0%{
        opacity: 0
    }
    100%{
        opacity: 1
    }
}